import systemIcon from "@/assets/gangtanhao.png";
import headPortrait from "@/assets/icon.png";
import phoneIcon from "@/assets/xiaoshouji.png";
import weChatIcon from "@/assets/xiaoweixin.png";
import Copy from "@/assets/copy.png";
import Clipboard from "clipboard";
// import kf1 from "@/assets/kf1.png";
// import kf2 from "@/assets/kf2.png";
// import kf3 from "@/assets/kf3.png";
// import kf4 from "@/assets/kf4.png";
// import kf5 from "@/assets/kf5.png";
// import kf6 from "@/assets/kf6.png";
export default {
  data() {
    return {
      topTitle: "联系客服",
      systemIcon: systemIcon,
      headPortrait: headPortrait,
      phoneIcon: phoneIcon,
      weChatIcon: weChatIcon,
      CopyIcon: Copy,
      toastVAl: false,
      toastText: "",
      list: [],
      // contentList: [
      //   {
      //     img: kf1,
      //     title: "加入社群",
      //     subTitle: "与算力大佬一起，交流挖矿技巧"
      //   },
      //   {
      //     img: kf2,
      //     title: "活动福利",
      //     subTitle: "平台活动早知道，更多福利等你解释"
      //   },
      //   {
      //     img: kf3,
      //     title: "收益通知",
      //     subTitle: "挖矿收益及时提醒，更有规划"
      //   },
      //   {
      //     img: kf4,
      //     title: "购买建议",
      //     subTitle: "提供定制化购买建议，助力您安心挣更多"
      //   },
      //   {
      //     img: kf5,
      //     title: "挖矿知识",
      //     subTitle: "定期提供挖矿知识科普，了解挖矿不盲从"
      //   },
      //   {
      //     img: kf6,
      //     title: "意见反馈",
      //     subTitle: "提供意见反馈卡，共建更好云算"
      //   }
      // ]
    };
  },
  mounted() {
    this.getContact();
  },
  methods: {
    getContact() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/index/contact",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    copyWord() {
      var that = this;
      var clipboard = new Clipboard(".serviceIcon");

      clipboard.on("success", function(e) {
        that.toastVAl = true;
        that.toastText = "复制成功";
        console.info("Text:", e.text);
        e.clearSelection();
      });

      clipboard.on("error", function(e) {
        console.error("Action:", e.action);
        console.error("Trigger:", e.trigger);
      });
    }
  }
};



